.backDrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.75);
}
.modal {
  padding: 15px;
  position: fixed;
  top: 15vh;
  left: 20%;
  width: 60%;
  overflow: hidden;
  z-index: 100;
  background-color: white;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  max-height: 70vh;
}
